<template>
  <div :style="{ background: background }">
    <button class="changeColor" v-on:click="toggleBackground">Change Background Color</button>
    <div class="container">
      <h1 :style="{ color: titleColor }">LEAVE YOUR COMMENT</h1>
      <div class="create-post">
        <label for="create-post" class="enter">Please enter your message:&nbsp;&nbsp;</label>
        <input type="text" id="create-post" v-model="text" placeholder="Enter your text here!">
        <button class ="post-btn" v-on:click="createPost">Post!</button>
      </div>
      <div v-if="showSuccessMessage" class="success-message">
        <p>You have successfully created a new message!</p>
        <button @click="closeSuccessMessage">&times;</button>
      </div>
      <div class="reminder">
        Reminder:
      </div>
      <div>
        1.You cannot post an empty message.<br>
        2.You cannot post a repetitive message.
      </div>
      <br>
      <p class="error" v-if="error">{{ error }}</p>
      <div class="posts-container">
        <div class="post"
            v-for="(post, index) in posts"
            v-bind:item="post"
            v-bind:index="index"
            v-bind:key="post._id"
            :class="{ 'post-highlight': colorChange }"
        >
          <button class="delete-btn" v-on:click="confirmDelete(post._id)">Delete</button>
          <div class="created-at">{{ formatDate(post.createdAt) }}</div>
          <p class="text">{{ post.text }}</p>
          <button class="update-btn" v-on:click="updatePost(post)">Update</button>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import PostService from '../PostService';

export default {
  name: 'PComponent',
  data(){
    return {
      posts: [],
      error: '',
      text: '',
      colorChange: false,
      showSuccessMessage: false,
      background: 'white',
      titleColor: 'darkgreen'
    }
  },
  
  async created(){
    try {
      this.posts = await PostService.getPosts();
    } catch(err) {
      this.error = err.message;
    }
  },

  methods:{
    async createPost() {
      if (!this.text) {
        // If the input is empty, show an error message and return
        this.error = 'Warning: No empty message is allowed. Please enter a message.';
        return;
      }
      
      if (this.posts.some(post => post.text === this.text)) {
        // If there is already a post with the same text, show an error message and return
        this.error = 'Warning: This message has already been posted. Please try again.';
        return;
      }
      this.error = '';
      // Otherwise, create the post and update the list of posts
      await PostService.insertPost(this.text);
      this.text = '';
      this.posts = await PostService.getPosts();
      this.showSuccessMessage = true;
    },

    async deletePost(id) {
      await PostService.deletePost(id);
      this.posts = await PostService.getPosts();
    },

    async updatePost(post) {
      const newText = prompt('Enter the updated text:', post.text);
      if (newText !== null && newText !== post.text) {
      post.text = newText;
      await PostService.updatePost(post._id, newText);
      this.posts = await PostService.getPosts();
      }
    },

    formatDate(date) {
      const options = { year: 'numeric', month: 'numeric', day: 'numeric' };
      return new Date(date).toLocaleDateString('en-US', options);
    },

    toggleColorChange() {
      this.colorChange = !this.colorChange;
    },

    toggleBackground() {
      this.background = this.background === 'white' ? '#9b4c98' : 'white';
      this.titleColor = this.background === 'white' ? 'darkgreen' : 'white';
    },

    closeSuccessMessage() {
      this.showSuccessMessage = false;
    },

    async confirmDelete(id) {
      if (confirm("Are you sure you want to delete this post?")) {
        await this.deletePost(id);
      }
    }
  }
};
</script>

