import axios from 'axios';
/* eslint-disable */
const url = 'api/posts';
const url2 = 'api/posts/';

class PostService{
    //get post
    static getPosts(){
        return new Promise(async(resolve, reject) => {
            try {
                const res = await axios.get(url);
                const data = res.data;
                resolve(
                    data.map(post => ({
                        ...post,
                        createdAt: new Date(post.createdAt)
                    }))
                );
            } catch(err){
                reject(err);
            }
        });
    }

    //create post
    static insertPost(text) {
        return axios.post(url,{
            text: text,
        });
    }

    //delete post
    static deletePost(id){
        return axios.delete(`${url2}${id}`);
    }

    // update post
    static updatePost(id, text) {
        return axios.put(`${url2}${id}`, {
            text: text
        });
    }

}

export default PostService;